.component{
	height: 100%;
	display:flex;
	flex-direction: column;
}
.component .progress{
    display: flex;
    flex-direction: column;
    width: 500px;
    line-height: 25%;
}
.component .progress span{
    font-weight: 600;
    font-family: 'Courier New', Courier, monospace;
}
.about{
    height: 100%;
    display: flex;
    flex-direction: column;
}
.about p{
    text-align: justify;
}

.skills{
    height: 99%;
    display: flex;
    flex-direction: column;
}
.skills .wrap{
    height: 100%;
    display: flex;
}
.skills .head{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.skills section{
    width: 20%;
    background: rgba(244,234,128,.5);
    border-bottom: 3px solid #662c3a;
    border-radius: 5px;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin:0 10px;
}
.skills img{
    width:30%;
}
.skills section ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 100%;
}

/*Education*/
.education{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
}
.education .university{
    display: flex;
    height: 17%;
}
.education .university .uIcon{
    width: 107px;
    height: 107px;
    border-radius: 5px;
}
.education .university .description{
    display: flex;
    flex-direction: column;
    margin-left: 5px;
    width: 100%;
}
.education .university .nameYear{
    display: flex;
    justify-content: space-between;
}
.education .university .nameYear span{
    font-weight: 600;
}

/*Contacts*/
.contacts{
    display: flex;
    flex-direction: column;
    height: 100%;
    align-items: center;
}
.contacts .contact{
    height: 100%;
    width: 20%;
}
.contacts .contact .textFields{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.contacts .contact .textArea{
    display: flex;
    flex-direction: column;
}
.contacts .contact span{
    font-weight: 600;
}
.contacts input[type="text"], .contacts input[type="tel"], .contacts input[type="email"]{
    height: 20px;
    border: 2px solid  #662c3a;
    border-radius: 3px;
}
.contacts textarea{
    border:2px solid #662c3a;
    border-radius: 3px;
    resize: none;
    height: 200px;
    font:inherit;
}
.contacts .sendForm{
    margin:5px auto;
}
.contacts .sendForm input[type="submit"]{
    font-family: Segoe UI;
    letter-spacing: 1.2px;
    background: rgba(244,234,128,.5);
    border: none;
    border-bottom: 2px solid #662c3a;
    border-radius: 3px;
    font-weight: 450;
    font-size: 1rem;
    width: 100%;
    cursor: pointer;
}