aside{
	display:flex;
	flex-direction: column;
	background: #d0d0d0;
	min-width: max-content;
	align-items:center;
	margin: 0 5px;
	padding:5px;
}
nav a{
	text-decoration:none;
	color: black;
}
nav a:hover{
	border-bottom: 1px solid #662c3a;
	transition: all ease-out 0.5s;
}
.pdata{
	display: flex;
	flex-direction: column;
	align-items: center;
	flex: 1;
}

.pdata img{
	height: 150px;
	width:150px;
	border-radius: 50%;
}
.pdata h1{
    font-weight: 650;
    font-size: 22px;
    padding:5px;
}
nav{
	flex:2;
	display:flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
}
footer{
	flex:0;
	width:100%;
	justify-content: space-around;
    display: flex;
}